async function getVaultData(wallet, address) {
  const balance = await wallet.provider.getBalance(VAULT_ADDRESS);

  const vault = new ethers.Contract(VAULT_ADDRESS, VAULT_ABI, wallet);

  const amountInToken = ethers.utils.parseEther('1'); // 1 TOKEN = ? COIN
  const amountOutCoin = await vault.tokenToCoin(amountInToken, address);
  const amountInCoin = ethers.utils.parseEther('1');
  const amountOutToken = await vault.coinToToken(amountInCoin, address);

  const basePrice = await vault.getBasePrice();

  return {
    balance,
    price: amountOutCoin,
    priceReverse: amountOutToken,
    basePrice: basePrice.toString()
  }
}

async function getSessionData(wallet) {
  const token = new ethers.Contract(TOKEN_ADDRESS, TOKEN_ABI, wallet);
  const balanceToken = await token.balanceOf(SESSION_ADDRESS);
  const session = new ethers.Contract(SESSION_ADDRESS, SESSION_ABI, wallet);
  const sss = await session.getStat();

  const history = [];
  const ns = sss[2].toNumber();
  if (ns > 1) {
    for (let i = 1; i < ns; i++) {
      const s = await session.getDataAtIndex(i);
      history.unshift({
        turn: s[0],
        totalShare: s[1],
        totalGenerated: s[2],
        profitPerShare: s[3],
        createdAt: s[4],
        snapshotPrice: s[5]
      });
    }
  }

  return {
    balanceToken,
    currentTurn: sss[0],
    currentTotalShare: sss[1],
    numSession: sss[2],
    history
  }
}

async function getOwnedNFTData(wallet, address) {
  const session = new ethers.Contract(SESSION_ADDRESS, SESSION_ABI, wallet);
  const nft = new ethers.Contract(NFT_ADDRESS, NFT_ABI, wallet);

  const sss = await session.getStat();
  const currentTurn = sss[0].toNumber();

  const numOwned = await nft.balanceOf(address);
  const nfts = [];
  for (let i = 0; i < numOwned; i++) {
    const id = await nft.tokenOfOwnerByIndex(address, i);
    const data = await session.getNFTData(id);

    const nftData1 = {
      id,
      startTurn: data[0],
      refId: data[1],
      share: data[2],
      uri: SERVER + '/nft?nftId=' + id
    }

    let metadata = {};

    try {
      const result = await axios.get(nftData1.uri);
      metadata = result.data;
    } catch (err) {
      console.error(err);
    }

    nftData1.metadata = metadata;

    const nftData2 = await getNFTHistory(wallet, id, currentTurn, nftData1.startTurn, nftData1.share);
    const nftData = Object.assign({}, nftData1, nftData2);
    nfts.push(nftData);
  }

  return nfts;
}

