async function getNFTHistory(wallet, nftId, currentTurn, startTurn, share) {
  const session = new ethers.Contract(SESSION_ADDRESS, SESSION_ABI, wallet);

  let totalUnclaimed = ethers.BigNumber.from(0);
  const unclaimedTurns = [];

  for (let turn = startTurn; turn < currentTurn; turn++) {
    const claimed = await session.getNFTClaimed(nftId, turn);
    if (claimed.gt(0)) continue;

    const data = await session.getDataAtIndex(turn);
    const profitPerShare = data[3];
    const profit = profitPerShare.mul(share);
    const unclaimed = profit.sub(claimed);
    totalUnclaimed = totalUnclaimed.add(unclaimed);

    if (claimed.toString() !== profit.toString()) {
      unclaimedTurns.push(turn);
    }
  }

  const ret = {
    totalUnclaimed,
    unclaimedTurns
  }
  return ret;
}

