async function addTokenToMetamask() {
  const tokenAddress = TOKEN_ADDRESS;
  const tokenSymbol = TOKEN;
  const tokenDecimals = 18;
  const tokenImage = TOKEN_IMAGE;

  try {
    // wasAdded is a boolean. Like any RPC method, an error may be thrown.
    const wasAdded = await window.ethereum.request({
      method: 'wallet_watchAsset',
      params: {
        type: 'ERC20', // Initially only supports ERC20, but eventually more!
        options: {
          address: tokenAddress, // The address that the token is at.
          symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
          decimals: tokenDecimals, // The number of decimals in the token
          image: tokenImage, // A string url of the token logo
        },
      },
    });

    if (wasAdded) {
      console.log('token added!');
    } else {
      console.log('token not added!');
    }
  } catch (error) {
    console.error(error);
  }
}

async function getCfxPrice(wallet, amountIn) {
  const router = new ethers.Contract(ROUTER_ADDRESS, ROUTER_ABI, wallet);
  const WETH_ADDRESS = await router.WETH();

  let path = [
    TOKEN_ADDRESS,
    WETH_ADDRESS
  ];

  if (amountIn.eq(0)) return ethers.BigNumber.from(0);
  let result = await router.getAmountsOut(amountIn, path);

  const price = result[1];
  return price;
}

async function getPosVaultData() {
  const wallet = WALLET;
  const pvr = new ethers.Contract(PVR_ADDRESS, PVR_ABI, wallet);

  const posPoolData = await posPool.userSummary(POSVAULT_ADDRESS);
  const stakeOnPVR = (await pvr.currentTotalStake()).toNumber();
  const stakeOnPP = posPoolData.available.toNumber();
  const retiredMode = (stakeOnPVR !== stakeOnPP);
  console.log({ stakeOnPVR, stakeOnPP, retiredMode });

  const dailyInterest = await pvr.getAverageDailyInterest();
  const dailyInterestCfx = await getCfxPrice(wallet, dailyInterest);
  const priceToken = await getCfxPrice(wallet, ethers.utils.parseEther('1'));

  const ret = {
    retiredMode,
    priceToken: ethers.utils.formatEther(priceToken.toString()),
    dailyInterest: ethers.utils.formatEther(dailyInterest.toString()),
    dailyInterest1000Cfx: ethers.utils.formatEther(dailyInterestCfx.toString()),
    dailyInterest1Cfx: ethers.utils.formatEther(dailyInterestCfx.div(1000).toString())
  };

  ret.apyCalc1 = Math.pow((1 + Number(ret.dailyInterest1Cfx)), 365);
  ret.apyCalc2 = ret.apyCalc1 - 1;
  ret.apy = ret.apyCalc2 * 100;

  const rhLength = await pvr.numRewardHistory();
  const rhIndex = rhLength.toNumber() - 1;
  const rh = await pvr.getRewardHistory(rhIndex);
  const lastRewardTime = rh[3].toNumber();
  ret.nextRewardAvailable = lastRewardTime + (24 * 3600);

  console.log(ret);

  return ret;
}

async function getPosVaultDataUser(wallet) {
  const pvr = new ethers.Contract(PVR_ADDRESS, PVR_ABI, wallet);

  const ret = {
  };

  const ownedCFX = await getWalletBalance(wallet.address);
  ret.ownedCFX = ethers.utils.formatEther(ownedCFX.toString());

  const pv = new ethers.Contract(POSVAULT_ADDRESS, POSVAULT_ABI, wallet);
  const ud = await pv.userData(wallet.address);

  const numStake = ud[0];
  let unstakeTime = ud[1].toNumber();
  // hardcode hack
  // phx pool lock period still 7 days, so it must be added 6 days to be 13 days
  unstakeTime += (3600 * 24 * 6);
  const wrLength = ud[2].toNumber();

  ret.numStake = ethers.utils.formatEther(numStake.toString());
  ret.unstakeTime = unstakeTime;
  ret.wrLength = wrLength;

  const pendingWithdraw = [];
  for (let i = 0; i < wrLength; i++) {
    const data = await pv.userWithdrawRequestData(wallet.address, i);
    const wrIndex = data[0].toNumber();
    const claimer = data[1];
    const withdrawAmount = data[2];
    const deadline = data[3].toNumber();
    const done = data[4];

    if (!done && claimer.toLowerCase() === wallet.address.toLowerCase()) {
      pendingWithdraw.push({
        wrIndex,
        withdrawAmount: ethers.utils.formatEther(withdrawAmount.toString()),
        deadline
      });
    }
  }
  ret.pendingWithdraw = pendingWithdraw;

  const ur = await pvr.getUnclaimedReward(wallet.address);
  ret.unclaimedReward = ethers.utils.formatEther(ur.toString());
  console.log('** getPosVaultDataUser **');
  console.log(ret);
  return ret;
}

