async function getLPVaultData() {
  const wallet = WALLET;
  const lpVault = new ethers.Contract(LPVAULT_ADDRESS, LPVAULT_ABI, wallet);

  // const staked = (await lpVault.currentTotalStake()).toString();
  const avgDailyInterest = await lpVault.getAverageDailyInterest();

  let dailyInterest = avgDailyInterest;
  const numHistory = (await lpVault.numRewardHistory()).toNumber();
  if (numHistory >= 3) {
    const N = numHistory - 1;
    const rowN = await lpVault.getRewardHistory(N);
    const rowNM1 = await lpVault.getRewardHistory(N - 1);
    const lastDeltaTime = rowN[3].sub(rowNM1[3]);
    const lastReward = rowN[2];
    const oneDay = 3600 * 24;
    const lastDailyInterest = lastReward.mul(oneDay).div(lastDeltaTime);
    dailyInterest = lastDailyInterest;
  }

  // const currentTotalStake = await lpVault.currentTotalStake();
  // const currentTurn = await lpVault.currentTurn();
  // const startRewardTime = await lpVault.startRewardTime();
  const lastRewardTime = await lpVault.lastRewardTime();
  const nextRewardAvailable = lastRewardTime.toNumber() + (3600 * 24);
  // const totalRewardPerStake = await lpVault.totalRewardPerStake();

  const dailyInterestCfx = await getCfxPrice(wallet, dailyInterest);
  const priceToken = await getCfxPrice(wallet, ethers.utils.parseEther('1'));

  // calc 1LP = ? CFX

  const totalSupplyLP = await pairLP.totalSupply();
  const reserves = await pairLP.getReserves();
  const reserve0 = reserves[0];
  // const reserve1 = reserves[1];

  console.log('** calc LP APY **');
  const singleLP = ethers.utils.parseEther('1');

  const oneLP2CFX = singleLP.mul(reserve0).div(totalSupplyLP); // price of 1 LP in CFX
  // const oneLP2TXT = singleLP.mul(reserve1).div(totalSupplyLP); // price of 1 LP in TXT

  const ret = {
    priceToken: ethers.utils.formatEther(priceToken.toString()),
    priceLP: ethers.utils.formatEther(oneLP2CFX.toString()),
    dailyInterest: ethers.utils.formatEther(dailyInterest.toString()),
    dailyInterestCFX: ethers.utils.formatEther(dailyInterestCfx.toString()),
    lastRewardTime: lastRewardTime.toNumber(),
    apy: 0,
    nextRewardAvailable
  };

  const interest = Number(ret.dailyInterestCFX) / Number(ret.priceLP);
  const apyCalc1 = Math.pow((1 + Number(interest)), 365);
  const apyCalc2 = apyCalc1 - 1;
  const apy = apyCalc2 * 100;
  // const apy = Math.pow((1 + Number(interest)), 365) * 100;

  ret.apyCalc1 = apyCalc1;
  ret.apyCalc2 = apyCalc2;
  ret.apy = apy;
  ret.interest = interest;

  console.log('LP Vault Data:');
  console.log(ret);

  return ret;
}

